import React from 'react'

import "./FormularioModal.css"

const FormularioModal = ( props ) => {

    const handleInputChange = event => {
        console.log(event)
        const { name, value } = event.target
        props.setInvitado({ ...props.invitado, [name]: value })
        console.log(props.invitado);
    }

    const handleCheckBoxChange = event => {
        //console.log(event.target.checked)
        const { name, checked } = event.target
        props.setInvitado({ ...props.invitado, [name]: (checked? '1' : '0') })
        //console.log(invitado);
    }      
    const handleRadioButtonChange = event => {
        //console.log(event.target.checked)
        const { name, value } = event.target
        //alert(value)
        props.setInvitado({ ...props.invitado, [name]: value })
        //console.log(invitado);
    }      
    return ( 
        <div className="form-group">

            {/* <header>
                <h1>
                    <a href="#">
                        <img src="http://tfgms.com/sandbox/dailyui/logo-1.png" alt="Authentic Collection"/>
                    </a>
                </h1>
            </header> */}
            <h1 class="text-center">Guiliana &amp; Adilmer</h1>
            <label htmlFor="campo1">Nombres de los adultos</label>
            <span>: &nbsp; </span>
            <input className="form-control" type="text"
                name="campo1" id="campo1"
                onChange={handleInputChange} value={props.invitado.campo1}
            />
            <label htmlFor="campo2" style={{ marginTop: `7px` }}>Cantidad de adultos</label>
            <span>: &nbsp; </span>
            <select className="custom-select mr-sm-2 combos" id="campo2" name="campo2" 
                    onChange={handleInputChange} 
                    value={props.invitado.campo2}>
                <option value={0}>0</option>
                <option value={1}>1</option>
                <option value={2}>2</option>
                <option value={3}>3</option>
                <option value={4}>4</option>
                <option value={5}>5</option>
                <option value={6}>6</option>
                <option value={7}>7</option>
                <option value={8}>8</option>
                <option value={9}>9</option>
                <option value={10}>10</option>
            </select>
            <br />



            <hr size="5" width="100%" ></hr>  

            <label htmlFor="campo3">Elegir plato entrante</label>
            <span>: &nbsp; </span>
            <input className="form-control"  type="text"
                name="campo3" id="campo3"
                onChange={handleInputChange} value={props.invitado.campo3}
            />
            "Suprema de salmón" o "Canelón de foie"
            Escribe tu nombre junto al plato elegido.
            <hr size="5" width="100%" ></hr>  
            <label htmlFor="campo4" style={{ marginTop: `7px` }}>Alergias / Intolerancias / Vegano</label>
            <span> &nbsp; </span>
            <input className="form-control" type="text"
                name="campo4" id="campo4"
                onChange={handleInputChange} value={props.invitado.campo4}
            /> 

            <label htmlFor="campo5" style={{ marginTop: `7px` }}>Prop&oacute;n un tema musical para la fiesta</label>
            <input className="form-control" type="text"
                name="campo5" id="campo5"
                onChange={handleInputChange} value={props.invitado.campo5}
            /> 

            <label htmlFor="campo6" style={{ marginTop: `7px` }}>Teléfono de contacto</label>
            <input className="form-control" type="text"
                name="campo6" id="campo6"
                onChange={handleInputChange} value={props.invitado.campo6}
            /> 


            <strong>Se requiere traslado:&nbsp;</strong>
            <br></br>
            <label> 
                <input
                name="campo7" id="campo7"
                checked={props.invitado.campo7 === "No" ? true : false}
                type="radio"
                value="No"
                onChange={handleRadioButtonChange}
                />&nbsp;No
            </label>
            &nbsp;&nbsp;/&nbsp;&nbsp;
            <label> 
                <input
                name="campo7" id="campo7"
                checked={props.invitado.campo7 === "Si" ? true : false}
                type="radio"
                value="Si"
                onChange={handleRadioButtonChange}
                />&nbsp;Si
            </label>
            <br></br>

            <strong style={{"color":"#ff7673"}}>Recuerda: El color azul eléctrico se reserva sólo para las damas de la novia.</strong>
        </div>

     );
}
 
export default FormularioModal;