import React, {useRef, useContext, useEffect, Fragment} from 'react';

import MensajeContexto from '../Mensaje/MensajeContexto';

import Timer from '../Countdown/Timer';

import Lottie from 'lottie-react';
import animationData from '../../data/Seccion_2/data.json';
import animationFondo from '../../data/Seccion_2_fondo/data.json';
import animationHojasDeFondo from '../../data/Seccion_2_hojas/data.json';

import "./Seccion_2.css"

const Seccion_2 = (props) => {

    const refSeccion2 = useRef(null);

    const { mostrarMensaje, updateMostrarMensaje } = useContext(MensajeContexto);

    const scrollCallback = (entries) => {
      if (entries[0].isIntersecting) {
          updateMostrarMensaje(false);
      }else{
          // do nothing
      }
    };

    useEffect(() => {
        // *** Grab the element related to this callback
        const { current } = refSeccion2;
        const observer = new IntersectionObserver(scrollCallback, {
            root: null,
            threshold: 1,
        });
        observer.observe(current);
        return () => {
            observer.disconnect(current); // *** Use the same element
        }
    }, [refSeccion2.current]); // *** Note dependency

    const styleFlorAnimada = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        left: "50%",
        width: '100%',
        transform: "translate(-50%, 100%)",
        zIndex: "200"
      };  

    const styleFoto_Texto = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        left: "50%",
        width: '100%',
        transform: "translate(-50%, 100%)",
        zIndex: "100"
    };  

    const styleFondoAnimado = {
      top: 0,
      padding: 0,
      border: 0,
      height: "100vh",
      position: "absolute",
      margin: "auto",
      left: "50%",
      width: '100%',
      transform: "translate(-50%, 100%)",
      zIndex: "0"
  };  

  let styleImage = {};

  if (props.ancho > 600) {
    styleImage = {
      top: 0,
      padding: 0,
      border: 0,
      height: "100vh",
      position: "absolute",
      margin: "auto",
      transform: "translate(-50%, 100%)",
      zIndex: "-100"
      };
  } else {
    styleImage = {
      top: 0,
      padding: 0,
      border: 0,
      height: "100vh",
      width: "100vw",
      position: "absolute",
      margin: "auto",
      transform: "translate(-50%, 100%)",
      zIndex: "-100"
      };
  }

    const interactivitySeccion2 = {
        mode: "scroll",
        actions: [
          {
            visibility: [0, 0.35],
            type: "stop",
            frames: [0],
          },
          {
            visibility: [0.35, 0.45],
            type: "play",
            frames: [0, 65],
          }
        ],
      };

    return (
        <Fragment>
          <div id="seccion2Box" ref={refSeccion2}></div>
          {/* Flores animadas */}
          <Lottie 
                loop = "false"
                animationData={animationData}
                interactivity={interactivitySeccion2}
                style={styleFlorAnimada}
            />  
          {/* Foto y texto*/}
           <Lottie 
                loop = "false"
                autoplay
                animationData={animationFondo}
                style={styleFoto_Texto}
            />
            {/* Hojas doradas de fondo */}
            <Lottie 
                loop 
                autoplay
                animationData={animationHojasDeFondo}
                style={styleFondoAnimado}
            />

            {/* Imagen de fondo */}
            <img id="fonddoSeccion2" src="./imgFondos/2.jpg" style={styleImage}></img>
            
            {/* <div className="contenedorTimer">
              <Timer deadline={"2024-01-01T00:00:00.000Z"} />
            </div> */}

        </Fragment>
    );
}
 
export default Seccion_2