import React, {Fragment} from 'react';

import Lottie from 'lottie-react';
import animationFlores from '../../data/Seccion_3/data.json';
import animationHojasDeFondo from '../../data/Seccion_3_hojas/data.json';

import "./Seccion_3.css"

const Seccion_3 = (props) => {

    let styleImage = {};

    if (props.ancho > 600) {
      styleImage = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 200%)",
        zIndex: "-100"
        };
    } else {
      styleImage = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        width: "100vw",
        position: "absolute",
        margin: "auto",
        transform: "translate(-50%, 200%)",
        zIndex: "-100"
        };
    }

    const style = {
        top: "0%",
        margin: '0px 0px 0px 0px',
        padding: '0px 0px 0px 0px',
        border: '0px 0px 0px 0px',
        height: "100vh",
        width: '100%',
        transform: "translate(0%, 0%)",
    };  

    const styleFondo = {
        top: 0,
        padding: 0,
        border: 0,
        height: "100vh",
        position: "absolute",
        margin: "auto",
        left: "50%",
        width: '100%',
        transform: "translate(-50%, 200%)",
        zIndex: "-100"
    };  

    const interactivitySeccion3 = {
        mode: "scroll",
        actions: [
            {
                visibility: [0, 0.35],
                type: "stop",
                frames: [0],
            },
            {
                visibility: [0.35, 0.45],
                type: "play",
                frames: [0, 65],
            }
        ],
    };

    return (
        <Fragment>
            {/* Anmaci{on de flores} */}
            <Lottie 
                loop = "false"
                animationData={animationFlores}
                interactivity={interactivitySeccion3}
                style={style}
            />
            {/* Hojas de doradas de fondo */}
            <Lottie 
                loop 
                autoplay
                animationData={animationHojasDeFondo}
                style={styleFondo}
            />
            {/* Imagen de fondo */}
            <img id="fonddoSeccion3" src="./imgFondos/3.jpg" style={styleImage}></img>
            {/* Texto */}
            <object id="textoSeccion3" type="image/svg+xml" data="./img/Iglesia.svg"></object> 
        </Fragment>
    );
}
 
export default Seccion_3